import React, { useEffect, useState, Fragment } from 'react';
import { Table, Container, Row, Col, Card, Modal, Form } from 'react-bootstrap';
import Input from './Inputs'
import axios from 'axios';
import environment from './../env';
import Util from './../Util.js'

const Compras = () => {
    const [valorCompra, setValorCompra] = useState(0)
    const [insumosCompra, setInsumosCompra] = useState([])
    const [cantidadinsumoSeleccionado, setCantidadinsumoSeleccionado] = useState(0)
    const [insumoSeleccionado, setInsumoSeleccionado] = useState(null)
    const [insumos, setInsumos] = useState([])
    const [compras, setCompras] = useState([])
    const [proveedores, setProveedores] = useState([])
    const [comprasOriginales, setComprasOriginales] = useState([])
    const [showNuevo, setShowNuevo] = useState(false)
    const [showAgregarProducto, setShowAgregarProducto] = useState(false);
    const [compra, setCompra] = useState({ nombre: "", ubicacion: "", mail: "" });
    const [compraDetalle, setCompraDetalle] = useState({ proveedor: {}, insumos: [] })
    const [showDetalleCompra, setShowDetalleCompra] = useState(false)
    const [forma, setForma] = useState("")

    const openAgregarProducto = () => setShowAgregarProducto(true);
    const closeModalAgregarProducto = () => {
        setShowAgregarProducto(false);
        setInsumoSeleccionado(null)
    }

    const closeModalDetalleCompra = () => {
        setShowDetalleCompra(false)
    }

    const mostrarDetalleCompra = (value) => {
        setCompraDetalle(value)
        setShowDetalleCompra(true)
    }

    const cambiarinsumoSeleccionado = (id) => {
        // eslint-disable-next-line
        var ins = insumos.filter(p => p.insumoId == id);
        if (ins != null) {
            setInsumoSeleccionado(ins[0]);
        }
    }

    const agregarProducto = () => {
        var array = insumosCompra;
        var copia = insumoSeleccionado;
        var valorTemp = valorCompra;
        copia.cantidad = Number(cantidadinsumoSeleccionado);
        array.push(copia)
        valorTemp += (Number(cantidadinsumoSeleccionado) * Number(copia.costo));
        setValorCompra(valorTemp)
        setInsumosCompra(array)
        compra.insumos = insumosCompra
        closeModalAgregarProducto()
        compra.total = valorTemp
    }

    const cargarInsumos = (proveedorId) => {
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/insumos/" + proveedorId, { withCredentials: true }).then((data) => {
            console.log(data.data)
            setInsumos(data.data.data)
        }).catch((e) => {
            console.log(e)
        })
    }

    function crear() {
        setCompra({ fecha: "", total: "", compra_proveedorId: "" })
        setShowNuevo(true)
    }
    function hide() {
        if (showNuevo) {
            setShowNuevo(false)
        }
    }
    function enviarDatos() {
        if (showNuevo) {
            axios.post(environment.baseApiUrl + "/compras", compra).then((data) => {
                setShowNuevo(false)
                setCompra({ fecha: "", total: "", compra_proveedorId: "" })
                setForma('')
                setValorCompra(0)
                setInsumosCompra([])
                cargarCompras()
            })
        }

    }
    function filtrar(event) {
        var keyword = event.target.value
        setCompras(comprasOriginales.filter(c =>
            c.nombre.toLowerCase().includes(keyword.toLowerCase()) || c.ubicacion.toLowerCase().includes(keyword.toLowerCase()) || c.mail.toLowerCase().includes(keyword.toLowerCase())))

    }
    function onChange(event, id) {
        compra[id] = event.target.value
        var temp;
        // eslint-disable-next-line
        if (compra.formaPago == "cuenta") {
            temp = <Col md="8" className=" d-flex justify-content-center"><Input type="date" label="A pagar el" value={compra.fechaPendiente} onChange={e => onChange(e, "fechaPendiente")}></Input></Col>;
        }
        setForma(temp)
    }
    function cargarCompras() {
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/compras    ", { withCredentials: true }).then((data) => {
            setCompras(data.data.data)
            setComprasOriginales(data.data.data)
        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }
    function cargarProveedores() {
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/proveedores", { withCredentials: true }).then((data) => {
            setProveedores(data.data)

        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }
    useEffect(() => {
        cargarCompras()
        cargarProveedores()
    }, [])

    return (

        <Fragment>
            <Modal show={showDetalleCompra} onHide={closeModalDetalleCompra}>
                <Modal.Header className="d-block">
                    <Modal.Title className="text-center">Detalle de Compra</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Row><Col><b>Fecha:</b> {Util.formatDate(compraDetalle.fecha)}</Col></Row>
                    <Row><Col><b>Proveedor:</b> {compraDetalle.proveedor.nombre}</Col></Row>
                    <Row><Col><b>Total:</b> ${compraDetalle.total}</Col></Row>
                    <div className="itemProducto">
                        {compraDetalle.insumos.map((value, index) => {
                            return <div key={index} className="text-center">{value.insumo.nombre} a ${value.precio} X {value.cantidad}</div>
                        })}
                    </div>

                </Modal.Body>
            </Modal>

            <Modal show={showAgregarProducto} onHide={closeModalAgregarProducto}>
                <Modal.Header className="d-block">
                    <Modal.Title className="text-center">Agregar Insumo</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form.Group controlId="exampleForm.ControlSelect1">
                        <Form.Label>Insumos</Form.Label>
                        <Form.Control as="select" defaultValue="0" onChange={(e) => cambiarinsumoSeleccionado(e.target.value)}>
                            <option value="0" disabled>Seleccione un insumo</option>
                            {insumos.map((value, index) => {
                                return <option key={index} value={value.insumoId}>{value.nombre}</option>
                            })}
                        </Form.Control>
                    </Form.Group>
                    {
                        insumoSeleccionado != null &&
                        <Fragment>
                            <div>Costo: ${insumoSeleccionado.costo}</div>
                        </Fragment>
                    }
                    <div className="fg" style={{ height: 60, marginTop: "0.5rem" }}>
                        <input type="number" placeholder=" " style={{ width: "100%" }} onChange={(e) => setCantidadinsumoSeleccionado(e.target.value)}></input>
                        <label>Cantidad</label>
                    </div>
                    <div className="text-center">
                        <button onClick={agregarProducto}>Agregar</button>
                    </div>
                </Modal.Body>
            </Modal>

            <Container className='mt-3'>
                {!showNuevo ? (
                    <Fragment>
                        <Row className="mb-3">
                            <Col>
                                <Card style={{ display: "inline-block", width: "100%" }}>
                                    <Row>
                                        <Col xs={3} md={2} ><button onClick={crear}>Agregar</button></Col>
                                        <Col xs={9} md={8}><input onChange={filtrar} className="search-field" placeholder="Escribe lo que deseas buscar"></input></Col>
                                        <Col xs={0} md={2} style={{ textAlign: "end" }}></Col>
                                    </Row>
                                </Card>
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                <Table bordered hover>
                                    <thead>
                                        <tr>
                                            <th width="20%">Fecha</th>
                                            <th width="40%">Proveedor</th>
                                            <th width="40%">Total</th>
                                            <th width="40%">Acciones</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Array.isArray(compras) && (
                                            compras.map((value, index) => {
                                                return <tr key={index} >
                                                    <td><div className="td-tag">Fecha</div>{Util.formatDate(value.fecha)}</td>
                                                    <td><div className="td-tag">Proveedor</div>{value.proveedor.nombre}</td>
                                                    <td><div className="td-tag">Total</div>${value.total}</td>
                                                    <td><div className="td-tag">Acciones</div><button className="borderless" onClick={e => mostrarDetalleCompra(value)}>Detalle</button></td>
                                                </tr>
                                            })
                                        )}
                                    </tbody>
                                </Table>
                            </Col>
                        </Row>
                    </Fragment>

                ) : (
                        <Fragment>
                            <Row className="mb-3">
                                <Col>
                                    <Card style={{ display: "inline-block", width: "100%" }}>
                                        <Row>
                                            <Col xs={3} md={2}><button onClick={hide}>Volver</button></Col>
                                            <Col xs={6} md={8} style={{ display: 'flex', alignItems: "center", justifyContent: 'center' }}><h5>Nueva compra</h5></Col>
                                            <Col xs={3} md={2}></Col>
                                        </Row>
                                    </Card>
                                </Col>
                            </Row>
                            <Card className="py-4">
                                <Row className="justify-content-center">
                                    <Col md="8" className=" d-flex justify-content-center"><Input type="date" label="Fecha" value={compra.fecha} onChange={e => onChange(e, "fecha")}></Input></Col>
                                    <Col md="8" className=" d-flex justify-content-center">
                                        <Form.Group controlId="exampleForm.ControlSelect1">
                                            <Form.Label>Proveedor</Form.Label>
                                            <Form.Control as="select" defaultValue="0" style={{ width: "250px" }}
                                                onChange={(e) => { cargarInsumos(e.target.value); compra['compra_proveedorId'] = Number(e.target.value); }}>
                                                <option value="0" disabled>Seleccione un proveedor</option>
                                                {proveedores.map((value, index) => {
                                                    return <option key={index} value={value.proveedorId}>{value.nombre}</option>
                                                })}
                                            </Form.Control>
                                        </Form.Group>
                                    </Col>
                                    <Col md="8" className=" d-flex justify-content-center">
                                        <Form.Group controlId="exampleForm.ControlSelect1">
                                            <Form.Label>Forma de Pago</Form.Label>
                                            <Form.Control as="select" defaultValue="0" style={{ width: "250px" }} onChange={e => onChange(e, "formaPago")}>
                                                <option value="0" disabled>Seleccione una forma de pago</option>
                                                <option value="efectivo">Efectivo</option>
                                                <option value="cuenta">A cuenta</option>
                                            </Form.Control>
                                        </Form.Group>
                                    </Col>
                                    {forma}
                                </Row>
                                <Row className="mt-3">
                                    <Col className="justify-content-center text-right"><h5>Insumos</h5></Col>
                                    <Col style={{ alignSelf: "center" }}><button className="compact" onClick={openAgregarProducto}>Agregar</button></Col>
                                </Row>
                                <Row>
                                    {insumosCompra ? (
                                        <Col className="itemProducto">{
                                            insumosCompra.map((value, index) => {
                                                return <div key={index} className="text-center">{value.nombre} X {value.cantidad}</div>
                                            })
                                        }

                                        </Col>
                                    ) : (
                                            <Col className="text-center py-5">
                                                <div>No hay insumos cargados</div>
                                            </Col>

                                        )}
                                </Row>
                                <Row>
                                    <Col className="text-center mt-5"><h5>Subtotal: ${valorCompra}</h5></Col>
                                </Row>
                                <Row className="justify-content-center mt-5">
                                    <button onClick={enviarDatos}>Confirmar Compra</button>
                                </Row>
                            </Card>
                        </Fragment>
                    )}
            </Container>
        </Fragment >
    )
}
export default Compras;