import React, { useEffect, useState, Fragment } from 'react';
import { Table, Container, Row, Col, Card } from 'react-bootstrap';
import Input from './Inputs'
import axios from 'axios';
import environment from './../env'
const Proveedores = () => {
    const [proveedores, setProveedores] = useState([])//Productos
    // eslint-disable-next-line
    const [proveedoresOriginales, setProveedoresOriginales] = useState([]);//Insumos de verdad
    const [showNuevo, setShowNuevo] = useState(false);
    const [showEditar, setShowEditar] = useState(false);
    //const [showInsumos, setShowInsumos] = useState(false);
    // eslint-disable-next-line
    const [showAgregarInsumo, setShowAgregarInsumo] = useState(false);
    const [proveedor, setProveedor] = useState({ nombre: "", telefono: "", email: "" });//Modelo usado cuando se agrega
    // eslint-disable-next-line
    const [nuevoInsumo, setNuevoInsumo] = useState(null);



    const crear = () => {
        setProveedor({ nombre: "", telefono: "", email: "", saldo: 0 })
        setShowNuevo(true)
    }
    const hide = () => {
        if (showNuevo) {
            setShowNuevo(false)
        } else if (showEditar) {
            setShowEditar(false)
        }
    }

    function editar(index) {
        setProveedor(proveedores[index])
        if (showNuevo) {
            setShowEditar(false)
        } else {
            setShowEditar(true)
        }
    }
    function enviarDatos() {
        if (showNuevo) {
            axios.post(environment.baseApiUrl + "/proveedores", proveedor).then((data) => {
                setShowNuevo(false)
                setProveedor({ nombre: "", telefono: "", email: "" })
                cargarProveedores()
            })
        } else if (showEditar) {
            axios.patch(environment.baseApiUrl + "/proveedores/" + proveedor.proveedorId, proveedor).then((data) => {
                setShowEditar(false)
                setProveedor({ nombre: "", telefono: "", email: "" })
                cargarProveedores();
            })
        }

    }
    function filtrar(event) {
        // eslint-disable-next-line
        var keyword = event.target.value
        //setInsumos(proveedores.filter(c =>
        //  c.nombre.toLowerCase().includes(keyword.toLowerCase()) || c.ubicacion.toLowerCase().includes(keyword.toLowerCase()) || c.mail.toLowerCase().includes(keyword.toLowerCase())))

    }
    function onChange(event, id) {
        proveedor[id] = event.target.value
    }

    function cargarProveedores() {//CargarProductos
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/proveedores", { withCredentials: true }).then((data) => {
            setProveedores(data.data)
            setProveedoresOriginales(data.data)
        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }
    useEffect(() => {
        cargarProveedores();
    }, [])

    return (

        <Container className='mt-3'>
            {!showNuevo && !showEditar ? (
                <Fragment>
                    <Row className="mb-3">
                        <Col>
                            <Card style={{ display: "inline-block", width: "100%" }}>
                                <Row>
                                    <Col xs={3} md={2} ><button onClick={crear}>Agregar</button></Col>
                                    <Col xs={9} md={8}><input onChange={filtrar} className="search-field" placeholder="Escribe lo que deseas buscar"></input></Col>
                                    <Col xs={0} md={2} style={{ textAlign: "end" }}></Col>
                                </Row>
                            </Card>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Table bordered hover>
                                <thead>
                                    <tr>
                                        <th width="20%">Nombre</th>
                                        <th width="20%">Teléfono</th>
                                        <th width="20%">Email</th>

                                    </tr>
                                </thead>
                                <tbody>
                                    {Array.isArray(proveedores) && (

                                        proveedores.map((value, index) => {
                                            return <tr key={index} >
                                                <td onClick={e => editar(index)}><div className="td-tag">Nombre</div>{value.nombre}</td>
                                                <td onClick={e => editar(index)}><div className="td-tag">Teléfono</div>{value.telefono}</td>
                                                <td onClick={e => editar(index)}><div className="td-tag">Email</div>{value.email}</td>
                                            </tr>
                                        })

                                    )}



                                </tbody>
                            </Table>
                        </Col>
                    </Row>
                </Fragment>

            ) : (
                    <Fragment>
                        <Row className="mb-3">
                            <Col>
                                <Card style={{ display: "inline-block", width: "100%" }}>
                                    <Row>
                                        <Col xs={3} md={2}><button onClick={hide}>Volver</button></Col>
                                        <Col xs={6} md={8} style={{ display: 'flex', alignItems: "center", justifyContent: 'center' }}><h5>{(showNuevo) ? (<div>Agregar proveedor</div>) : (<div>Editar</div>)}</h5></Col>
                                        <Col xs={3} md={2}></Col>
                                    </Row>
                                </Card>
                            </Col>
                        </Row>
                        <Card className="py-4">
                            <Row>
                                <Col className=" d-flex justify-content-center"><Input type="text" label="Nombre" value={proveedor.nombre} onChange={e => onChange(e, "nombre")}></Input></Col>
                                <Col className=" d-flex justify-content-center"><Input type="text" label="Teléfono" value={proveedor.telefono} onChange={e => onChange(e, "telefono")}></Input></Col>
                                <Col className=" d-flex justify-content-center"><Input type="text" label="Email" value={proveedor.email} onChange={e => onChange(e, "email")}></Input></Col>
                            </Row>


                            <Row className="justify-content-center mt-5">
                                <button onClick={enviarDatos}>Guardar</button>
                            </Row>
                        </Card>
                    </Fragment>
                )}
        </Container>
    )
}
export default Proveedores;