import React, { useEffect, useState, Fragment } from 'react';
import { Table, Container, Row, Col, Card } from 'react-bootstrap';
import Input from './Inputs'
import axios from 'axios';
import environment from './../env';

const Clientes = () => {
    const [clientes, setClientes] = useState([])
    const [clientesOriginales, setClientesOriginales] = useState([])
    const [showNuevo, setShowNuevo] = useState(false)
    const [showEditar, setShowEditar] = useState(false);
    const [cliente, setCliente] = useState({ nombre: "", ubicacion: "", mail: "" });

    function crear() {
        setCliente({ nombre: "", ubicacion: "", mail: "" })
        setShowNuevo(true)
    }
    function hide() {
        if (showNuevo) {
            setShowNuevo(false)
        } else if (showEditar) {
            setShowEditar(false)
        }
    }
    function editar(index) {
        setCliente(clientes[index])
        if (showNuevo) {
            setShowEditar(false)
        } else {
            setShowEditar(true)
        }
    }
    function enviarDatos() {
        if (showNuevo) {
            axios.post(environment.baseApiUrl + "/clientes", cliente).then((data) => {
                setShowNuevo(false)
                cargarClientes()
            })
        } else if (showEditar) {
            axios.patch(environment.baseApiUrl + "/clientes/" + cliente.id, cliente).then((data) => {
                setShowEditar(false)
                cargarClientes()
            })
        }

    }
    function filtrar(event) {
        var keyword = event.target.value
        setClientes(clientesOriginales.filter(c =>
            c.nombre.toLowerCase().includes(keyword.toLowerCase()) || c.ubicacion.toLowerCase().includes(keyword.toLowerCase()) || c.mail.toLowerCase().includes(keyword.toLowerCase())))

    }
    function onChange(event, id) {
        cliente[id] = event.target.value
    }
    function cargarClientes() {
        setCliente({ nombre: "", ubicacion: "", mail: "" });
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/clientes", { withCredentials: true }).then((data) => {
            setClientes(data.data)
            setClientesOriginales(data.data)
        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }
    useEffect(() => {
        cargarClientes()
    }, [])

    return (

        <Container className='mt-3'>
            {!showNuevo && !showEditar ? (
                <Fragment>
                    <Row className="mb-3">
                        <Col>
                            <Card style={{ display: "inline-block", width: "100%" }}>
                                <Row>
                                    <Col xs={3} md={2} ><button onClick={crear}>Agregar</button></Col>
                                    <Col xs={9} md={8}><input onChange={filtrar} className="search-field" placeholder="Escribe lo que deseas buscar"></input></Col>
                                    <Col xs={0} md={2} style={{ textAlign: "end" }}></Col>
                                </Row>
                            </Card>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Table bordered hover>
                                <thead>
                                    <tr>
                                        <th width="20%">Nombre</th>
                                        <th width="40%">Dirección</th>
                                        <th width="40%">Email</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {clientes.map((value, index) => {
                                        return <tr key={index} onClick={e => editar(index)}>
                                            <td><div className="td-tag">Nombre</div>{value.nombre}</td>
                                            <td><div className="td-tag">Ubicación</div>{value.ubicacion}</td>
                                            <td><div className="td-tag">Email</div>{value.mail}</td>
                                        </tr>
                                    })}

                                </tbody>
                            </Table>
                        </Col>
                    </Row>
                </Fragment>

            ) : (
                    <Fragment>
                        <Row className="mb-3">
                            <Col>
                                <Card style={{ display: "inline-block", width: "100%" }}>
                                    <Row>
                                        <Col xs={3} md={2}><button onClick={hide}>Volver</button></Col>
                                        <Col xs={6} md={8} style={{ display: 'flex', alignItems: "center", justifyContent: 'center' }}><h5>Nuevo Cliente</h5></Col>
                                        <Col xs={3} md={2}></Col>
                                    </Row>
                                </Card>
                            </Col>
                        </Row>
                        <Card className="py-4">
                            <Row>
                                <Col className=" d-flex justify-content-center"><Input type="text" label="Nombre" value={cliente.nombre} onChange={e => onChange(e, "nombre")}></Input></Col>
                                <Col className=" d-flex justify-content-center"><Input type="text" label="Ubicacion" value={cliente.ubicacion} onChange={e => onChange(e, "ubicacion")}></Input></Col>
                                <Col className=" d-flex justify-content-center"><Input type="text" label="Correo electrónico" value={cliente.mail} onChange={e => onChange(e, "mail")}></Input></Col>
                            </Row>
                            <Row className="justify-content-center mt-3">
                                <button onClick={enviarDatos}>Guardar</button>
                            </Row>
                        </Card>
                    </Fragment>
                )}
        </Container>
    )
}
export default Clientes;