import React from 'react';
import './App.css';
import NavBar from './Componentes/Navbar';
import Clientes from './Componentes/Clientes';
import {
  BrowserRouter as Router,
  Switch
} from "react-router-dom";
import Login from './Componentes/Login';
import { UsuarioProvider, useUsuario } from './contexts/AppContext';
import GuardedRoute from './Componentes/GuardedRoute';
import Ventas from './Componentes/Ventas';
import Insumos from './Componentes/Insumos';
import Productos from './Componentes/Productos';
import Landing from './Componentes/Landing'
import Inicio from './Componentes/Inicio';
import Proveedores from './Componentes/Proveedores';
import Compras from './Componentes/Compras';
import Cuenta from './Componentes/Cuenta';

export default () =>
  <UsuarioProvider>
    <App></App>
  </UsuarioProvider>


function App() {
  const { usuario } = useUsuario();
  return (
    <Router>
      <NavBar></NavBar>
      <Switch>
        <GuardedRoute
          component={Login}
          path='/login'
          redirectTo='/'
          auth={usuario == null}></GuardedRoute>

        <GuardedRoute
          component={Clientes}
          path='/Clientes' redirectTo='/login'
          auth={usuario != null}></GuardedRoute>

        <GuardedRoute
          component={Ventas}
          path='/Ventas'
          redirectTo='/login'
          auth={usuario != null}></GuardedRoute>

        <GuardedRoute
          component={Compras}
          path='/Compras'
          redirectTo='/login'
          auth={usuario != null}></GuardedRoute>

        <GuardedRoute
          component={Insumos}
          path='/Insumos'
          redirectTo='/login'
          auth={usuario != null}></GuardedRoute>

        <GuardedRoute
          component={Productos}
          path='/Stock'
          redirectTo='/login'
          auth={usuario != null}></GuardedRoute>

        <GuardedRoute
          component={Proveedores}
          path='/Proveedores'
          redirectTo='/login'
          auth={usuario != null}></GuardedRoute>

        <GuardedRoute
          component={Cuenta}
          path='/Cuenta'
          redirectTo='/login'
          auth={usuario != null}></GuardedRoute>

        <GuardedRoute
          component={Inicio}
          path='/inicio'
          redirectTo='/login'
          auth={usuario != null}></GuardedRoute>

        <GuardedRoute
          component={Landing}
          path='/'
          redirectTo='/inicio'
          auth={usuario == null}></GuardedRoute>


      </Switch>
    </Router>
  );
}

//export default App;
