import React, { useState, useMemo, useEffect } from 'react';
import axios from 'axios';
import environment from './../env'

const UsuarioContext = React.createContext();
export function UsuarioProvider(props) {
    const [usuario, setUsuario] = useState(localStorage.getItem('usuario'))
    const [loginError, setLoginError] = useState(null)
    //const [id, setId] = useState(null)

    useEffect(() => {
        function cargarUsuario() {

            setUsuario(localStorage.getItem('usuario'));
        }
        cargarUsuario();
    }, []);

    const postLogin = (user) => {
        axios.post(environment.baseApiUrl + "/login", user, { withCredentials: true })
            .then((data) => {
                setUsuario(data.data.email)
                localStorage.setItem('usuario', data.data.email);
                setLoginError(null)
            }).catch((e) => {

            })

    }

    const login = (user) => {
        axios.defaults.withCredentials = true;
        axios.get(environment.baseUrl + "/sanctum/csrf-cookie", { withCredentials: true }).then((data) => {
            postLogin(user)
        })
            .catch((e) => {

            })


    }
    function logout() {
        setUsuario(null)
        localStorage.removeItem('usuario')
    }

    const value = useMemo(() => {
        return ({
            usuario,
            login,
            logout,
            loginError
        })
        // eslint-disable-next-line
    }, [usuario, loginError]);

    return (<UsuarioContext.Provider value={value} {...props} />)
}
export function useUsuario() {
    const context = React.useContext(UsuarioContext);
    if (!context) {
        throw new Error('useUsuario no puede ser nulo')
    }
    return context;
}