import React, { useState } from 'react';
import { Card } from 'react-bootstrap';
import './Login.css'
import Input from './Inputs'
import { useUsuario } from '../contexts/AppContext';
const Login = () => {
    // eslint-disable-next-line
    const [user, setUser] = useState({ email: "", password: "" })
    const { login, loginError } = useUsuario();
    return (
        <Card className='login'>
            <h5 style={{ textAlign: "center", marginBottom: 30 }}>Iniciar Sesión</h5>
            <Input label="Nombre de Usuario" type="text" id="usuario"
                onChange={e => onChange(e, "email")}></Input>
            <Input label="Contraseña" type="password" id="password"
                onChange={e => onChange(e, "password")}></Input>
            {loginError != null && (<p style={{
                fontSize: '13px',
                color: '#f92a2a',
                transition: '.5s'
            }}>{loginError}</p>)}
            <button onClick={e => login(user)} style={{ marginTop: 40 }}>Iniciar</button>

        </Card>
    )
    function onChange(event, id) {
        user[id] = event.target.value
    }
}
export default Login;