import React, { useEffect, useState, Fragment } from 'react';
import { Table, Container, Row, Col, Card, Form } from 'react-bootstrap';
import Input from './Inputs'
import axios from 'axios';
import environment from './../env'
const Insumos = () => {
    const [proveedores, setProveedores] = useState([])
    const [insumos, setInsumos] = useState([])
    const [insumosOriginales, setInsumosOriginales] = useState([])
    const [showNuevo, setShowNuevo] = useState(false)
    const [showEditar, setShowEditar] = useState(false);
    const [insumo, setInsumo] = useState({ nombre: "", cantidad: "", unidad: "", costo: "" });


    function traerProveedores() {
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/proveedores", { withCredentials: true }).then((data) => {
            setProveedores(data.data)

        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }

    function crear() {
        setInsumo({ nombre: "", ubicacion: "", mail: "" })
        setShowNuevo(true)
    }
    function hide() {
        if (showNuevo) {
            setShowNuevo(false)
        } else if (showEditar) {
            setShowEditar(false)
        }
    }
    function editar(index) {
        setInsumo(insumos[index])
        if (showNuevo) {
            setShowEditar(false)
        } else {
            setShowEditar(true)
        }
    }
    function enviarDatos() {
        if (showNuevo) {
            axios.post(environment.baseApiUrl + "/insumos", insumo).then((data) => {
                setShowNuevo(false)
                setInsumo({ nombre: "", cantidad: "", unidad: "", costo: "" })
                cargarinsumos()
            })
        } else if (showEditar) {
            axios.patch(environment.baseApiUrl + "/insumos/" + insumo.insumoId, insumo).then((data) => {
                setShowEditar(false)
                setInsumo({ nombre: "", cantidad: "", unidad: "", costo: "" })
                cargarinsumos()
            })
        }

    }
    function filtrar(event) {
        var keyword = event.target.value
        setInsumos(insumosOriginales.filter(c =>
            c.nombre.toLowerCase().includes(keyword.toLowerCase()) || c.ubicacion.toLowerCase().includes(keyword.toLowerCase()) || c.mail.toLowerCase().includes(keyword.toLowerCase())))

    }
    function onChange(event, id) {
        insumo[id] = event.target.value
    }
    function cargarinsumos() {
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/insumos", { withCredentials: true }).then((data) => {
            setInsumos(data.data.data)
            setInsumosOriginales(data.data.data)
        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }
    useEffect(() => {
        traerProveedores()
        cargarinsumos()
    }, [])

    return (

        <Container className='mt-3'>
            {!showNuevo && !showEditar ? (
                <Fragment>
                    <Row className="mb-3">
                        <Col>
                            <Card style={{ display: "inline-block", width: "100%" }}>
                                <Row>
                                    <Col xs={3} md={2} ><button onClick={crear}>Agregar</button></Col>
                                    <Col xs={9} md={8}><input onChange={filtrar} className="search-field" placeholder="Escribe lo que deseas buscar"></input></Col>
                                    <Col xs={0} md={2} style={{ textAlign: "end" }}></Col>
                                </Row>
                            </Card>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Table bordered hover>
                                <thead>
                                    <tr>
                                        <th width="20%">Nombre</th>
                                        <th width="40%">Cantidad</th>
                                        <th width="40%">Costo</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {insumos.map((value, index) => {
                                        return <tr key={index} onClick={e => editar(index)}>
                                            <td><div className="td-tag">Nombre</div>{value.nombre}</td>
                                            <td><div className="td-tag">Cantidad</div>{value.cantidad}</td>
                                            <td><div className="td-tag">Costo</div>${value.costo}</td>
                                        </tr>
                                    })}

                                </tbody>
                            </Table>
                        </Col>
                    </Row>
                </Fragment>

            ) : (
                    <Fragment>
                        <Row className="mb-3">
                            <Col>
                                <Card style={{ display: "inline-block", width: "100%" }}>
                                    <Row>
                                        <Col xs={3} md={2}><button onClick={hide}>Volver</button></Col>
                                        <Col xs={6} md={8} style={{ display: 'flex', alignItems: "center", justifyContent: 'center' }}><h5>{(showNuevo) ? (<div>Nuevo Insumo</div>) : (<div>Editar Insumo</div>)}</h5></Col>
                                        <Col xs={3} md={2}></Col>
                                    </Row>
                                </Card>
                            </Col>
                        </Row>
                        <Card className="py-4">
                            <Row className="justify-content-center">
                                <Col md="8" className=" d-flex justify-content-center"><Input type="text" label="Nombre" value={insumo.nombre} onChange={e => onChange(e, "nombre")}></Input></Col>
                                <Col md="8" className=" d-flex justify-content-center"><Input type="text" label="Existencia" value={insumo.cantidad} readOnly={showEditar} onChange={e => onChange(e, "cantidad")}></Input></Col>
                                <Col md="8" className=" d-flex justify-content-center"><Input type="text" label="($)Costo" value={insumo.costo} onChange={e => onChange(e, "costo")}></Input></Col>
                                <Col md="8" className=" d-flex justify-content-center">
                                    <Form.Group controlId="exampleForm.ControlSelect1">
                                        <Form.Label>Proveedor</Form.Label>
                                        <Form.Control as="select" defaultValue="0" style={{ width: "250px" }} onChange={(e) => onChange(e, "proveedorId")}>
                                            <option value="0" disabled>Seleccione un proveedor</option>
                                            {proveedores.map((value, index) => {
                                                return <option key={index} value={value.proveedorId}>{value.nombre}</option>
                                            })}
                                        </Form.Control>
                                    </Form.Group></Col>
                            </Row>
                            <Row className="justify-content-center mt-3">
                                <button onClick={enviarDatos}>Guardar</button>
                            </Row>
                        </Card>
                    </Fragment>
                )}
        </Container>
    )
}
export default Insumos;