import React from 'react';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap'
import { Link, NavLink } from 'react-router-dom';
import { useUsuario } from '../contexts/AppContext';

const NavBar = () => {
    const rutas = [
        { nombre: "Clientes", path: "/Clientes", id: 1 },
        //{ nombre: "Ventas", path: "/Ventas" },
        { nombre: "Stock", path: "/Stock", id: 2 },
        { nombre: "Insumos", path: "/Insumos", id: 3 },
        { nombre: "Ventas", path: "/Ventas", id: 4 },
        { nombre: "Proveedores", path: "/Proveedores", id: 5 },
        { nombre: "Compras", path: "/Compras", id: 6 },
        { nombre: "Cuenta Corriente", path: "/Cuenta", id: 7 }
        //{ nombre: "Empleados", path: "/Insumos" },
        //{ nombre: "Cuenta Corriente", path: "/Insumos" }
    ];
    const { usuario, logout } = useUsuario();

    const navRoutes = rutas.map((ruta) => {
        return (<Nav.Link as={NavLink} eventKey={ruta.id} key={ruta.path} className='nav-link' to={ruta.path} activeclassname="active">{ruta.nombre}</Nav.Link>)
    })
    return (<Navbar collapseOnSelect expand="lg">
        <Navbar.Brand href="#home">Gestión</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
                {usuario != null && (
                    navRoutes
                )
                }

            </Nav>
            <Nav className="ml-auto">

                {usuario != null ? (
                    <NavDropdown alignRight title={usuario} id="collasible-nav-dropdown">
                        <NavDropdown.Item href="#action/3.3">Ajustes</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#" onClick={logout}>Cerrar Sesión</NavDropdown.Item>
                    </NavDropdown>
                ) : (<Nav.Link as={Link} className='nav-link' to='login' activeClassName="active">Iniciar Sesión</Nav.Link>)
                }

            </Nav>

        </Navbar.Collapse>
    </Navbar>)
}

export default NavBar;