import React, { useEffect, useState, Fragment } from 'react';
import { Table, Container, Row, Col, Card, Form, Modal } from 'react-bootstrap';
import Input from './Inputs'
import axios from 'axios';
import environment from './../env';
import Util from './../Util.js'

const Cuenta = () => {
    const [cobroOrPago, setCobroOrPago] = useState('')
    const [compras, setCompras] = useState([])
    const [comprasOriginales, setComprasOriginales] = useState([])
    const [showNuevo, setShowNuevo] = useState(false)
    const [compra, setCompra] = useState({ fecha: "", monto: "", tipo: "", descripcion: "", forma: "", fechaPendiente: "" });
    const [detalle, setDetalle] = useState({ compra: {}, venta: {} });
    const [showDetalle, setShowDetalle] = useState(false);

    function crear() {
        setCompra({})
        setShowNuevo(true)
    }

    const mostrarDetalle = (value) => {
        setDetalle(value)
        setShowDetalle(true)
    }
    const closeModalDetalle = () => {
        setShowDetalle(false)
    }
    function hide() {
        if (showNuevo) {
            setShowNuevo(false)
        }
    }

    function enviarDatos() {
        if (showNuevo) {
            axios.post(environment.baseApiUrl + "/cuentas", compra).then((data) => {
                setShowNuevo(false)
                setCompra({ fecha: "", monto: "", tipo: "", descripcion: "", forma: "", fechaPendiente: "" })
                setCobroOrPago('')
                cargarCompras()
            })
        }

    }
    function filtrar(event) {
        var keyword = event.target.value
        setCompras(comprasOriginales.filter(c =>
            c.nombre.toLowerCase().includes(keyword.toLowerCase()) || c.ubicacion.toLowerCase().includes(keyword.toLowerCase()) || c.mail.toLowerCase().includes(keyword.toLowerCase())))

    }
    function onChange(event, id) {
        compra[id] = event.target.value

        var temp;
        // eslint-disable-next-line
        if (compra.forma == 'cuenta' && compra.tipo == 'Ingreso') {
            temp = <Col md="8" className=" d-flex justify-content-center"><Input type="date" label="A cobrar el" value={compra.fechaPendiente} onChange={e => onChange(e, "fechaPendiente")}></Input></Col>;
            // eslint-disable-next-line
        } else if (compra.forma == 'cuenta' && compra.tipo == 'Egreso') {
            temp = <Col md="8" className=" d-flex justify-content-center"><Input type="date" label="A pagar el" value={compra.fechaPendiente} onChange={e => onChange(e, "fechaPendiente")}></Input></Col>;
        }
        setCobroOrPago(temp)
    }
    function cargarCompras() {
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/cuentas", { withCredentials: true }).then((data) => {
            setCompras(data.data.data)
            setComprasOriginales(data.data.data)
        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }

    useEffect(() => {
        cargarCompras()
    }, [])

    return (

        <Fragment>

            <Container className='mt-3'>
                {!showNuevo ? (
                    <Fragment>
                        <Modal show={showDetalle} onHide={closeModalDetalle}>
                            <Modal.Header className="d-block">
                                <Modal.Title className="text-center">Detalle</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <Row><Col><b>Fecha:</b> {Util.formatDate(detalle.fecha)}</Col></Row>
                                <Row><Col><b>Tipo:</b> {detalle.tipo}</Col></Row>
                                <Row><Col><b>Descripcion:</b> {detalle.descripcion}</Col></Row>
                                <Row><Col><b>Pago:</b> {detalle.forma}</Col></Row>
                                {detalle.compra != null && detalle.compra.proveedor &&
                                    <Row><Col><b>Proveedor:</b> {detalle.compra.proveedor.nombre}</Col></Row>
                                }
                                {detalle.venta != null && detalle.venta.cliente &&
                                    <Row><Col><b>Cliente:</b> {detalle.venta.cliente.nombre}</Col></Row>
                                }
                                {// eslint-disable-next-line
                                    detalle.fechaPendiente != null && detalle.tipo == "Egreso" && <Row><Col><b>Fecha de cobro:</b> {Util.formatDate(detalle.fechaPendiente)}</Col></Row>}
                                < Row > <Col><b>Monto:</b> ${detalle.monto}</Col></Row>
                                {detalle.compra != null &&
                                    <Row><Col className="text-center"><b>Detalle de compra</b></Col></Row>
                                }
                                {detalle.venta != null &&
                                    <Row><Col className="text-center"><b>Detalle de venta</b></Col></Row>
                                }
                                <div className="itemProducto">
                                    {detalle.compra != null && detalle.compra.insumos && detalle.compra.insumos.map((value, index) => {
                                        return <div key={index} className="text-center">{value.insumo.nombre} a ${value.precio} X {value.cantidad}</div>
                                    })}
                                    {detalle.venta != null && detalle.venta.producto_venta && detalle.venta.producto_venta.map((value, index) => {
                                        return <div key={index} className="text-center">{value.producto.nombre} a ${value.precio} X {value.cantidad}</div>
                                    })}
                                </div>

                            </Modal.Body>
                        </Modal>
                        <Row className="mb-3">
                            <Col>
                                <Card style={{ display: "inline-block", width: "100%" }}>
                                    <Row>
                                        <Col xs={3} md={2} ><button onClick={crear}>Agregar</button></Col>
                                        <Col xs={9} md={8}><input onChange={filtrar} className="search-field" placeholder="Escribe lo que deseas buscar"></input></Col>
                                        <Col xs={0} md={2} style={{ textAlign: "end" }}></Col>
                                    </Row>
                                </Card>
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                <Table bordered hover>
                                    <thead>
                                        <tr>
                                            <th width="20%">Fecha</th>
                                            <th width="20%">Descripción</th>
                                            <th width="20%">Tipo</th>
                                            <th width="20%">Forma de Pago</th>
                                            <th width="20%">Total</th>
                                            <th width="20%">Acciones</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Array.isArray(compras) && (
                                            compras.map((value, index) => {
                                                return <tr key={index} >
                                                    <td><div className="td-tag">Fecha</div>{Util.formatDate(value.fecha)}</td>
                                                    <td><div className="td-tag">Descripcion</div>{value.descripcion}</td>
                                                    <td><div className="td-tag">Tipo</div>{value.tipo}</td>
                                                    <td><div className="td-tag">Forma de pago</div>{value.forma}</td>
                                                    <td><div className="td-tag">Total</div>${value.monto}</td>
                                                    <td><div className="td-tag">Acciones</div><button className="borderless" onClick={e => mostrarDetalle(value)}>Detalle</button></td>
                                                </tr>
                                            })
                                        )}
                                    </tbody>
                                </Table>
                            </Col>
                        </Row>
                    </Fragment>

                ) : (
                        <Fragment>
                            <Row className="mb-3">
                                <Col>
                                    <Card style={{ display: "inline-block", width: "100%" }}>
                                        <Row>
                                            <Col xs={3} md={2}><button onClick={hide}>Volver</button></Col>
                                            <Col xs={6} md={8} style={{ display: 'flex', alignItems: "center", justifyContent: 'center' }}><h5>Nuevo registro</h5></Col>
                                            <Col xs={3} md={2}></Col>
                                        </Row>
                                    </Card>
                                </Col>
                            </Row>
                            <Card className="py-4">
                                <Row className="justify-content-center">
                                    <Col md="8" className=" d-flex justify-content-center"><Input type="date" label="Fecha" value={compra.fecha} onChange={e => onChange(e, "fecha")}></Input></Col>
                                    <Col md="8" className=" d-flex justify-content-center"><Input type="number" label="Monto" value={compra.monto} onChange={e => onChange(e, "monto")}></Input></Col>
                                    <Col md="8" className=" d-flex justify-content-center"><Input type="text" label="Descripcion" value={compra.descripcion} onChange={e => onChange(e, "descripcion")}></Input></Col>

                                    <Col md="8" className=" d-flex justify-content-center">
                                        <Form.Group controlId="exampleForm.ControlSelect1">
                                            <Form.Label>Tipo</Form.Label>
                                            <Form.Control as="select" defaultValue="0" style={{ width: "250px" }}
                                                onChange={e => onChange(e, "tipo")}>
                                                <option value="0" disabled>Seleccione un tipo</option>
                                                <option value="Ingreso">Ingreso</option>
                                                <option value="Egreso">Egreso</option>

                                            </Form.Control>
                                        </Form.Group>
                                    </Col>
                                    <Col md="8" className=" d-flex justify-content-center">
                                        <Form.Group controlId="exampleForm.ControlSelect1">
                                            <Form.Label>Forma de Pago</Form.Label>
                                            <Form.Control as="select" defaultValue="0" style={{ width: "250px" }} onChange={e => onChange(e, "forma")}>
                                                <option value="0" disabled>Seleccione una forma de pago</option>
                                                <option value="efectivo">Efectivo</option>
                                                <option value="cuenta">A cuenta</option>
                                            </Form.Control>
                                        </Form.Group>
                                    </Col>
                                    {cobroOrPago}
                                </Row>

                                <Row className="justify-content-center mt-5">
                                    <button onClick={enviarDatos}>Confirmar</button>
                                </Row>
                            </Card>
                        </Fragment>
                    )}
            </Container>
        </Fragment >
    )
}
export default Cuenta;