const Util = {

    formatDate: (date) => {
        // eslint-disable-next-line
        if (date == null || date.length == 0) return "";
        var newDate = date.substring(8, date.length)
        newDate += "-" + date.substring(5, 7)
        newDate += "-" + date.substring(0, 4)
        return newDate
    }

}


export default Util;