import React, { useEffect, useState, Fragment } from 'react';
import { Table, Container, Row, Col, Card, Modal, Form } from 'react-bootstrap';
import Input from './Inputs'
import axios from 'axios';
import environment from './../env'
const Productos = () => {
    const [insumos, setInsumos] = useState([])//Productos
    const [insumosOriginales, setInsumosOriginales] = useState([])//Productos
    const [insumosVerdaderos, setInsumosVerdaderos] = useState([]);//Insumos de verdad
    const [showNuevo, setShowNuevo] = useState(false);
    const [showEditar, setShowEditar] = useState(false);
    const [showAgregarInsumo, setShowAgregarInsumo] = useState(false);
    const [insumo, setInsumo] = useState({ nombre: "", cantidad: "", unidad: "", costo: 0, insumos: [] });//Modelo usado cuando se agrega
    const [modalInsumos, setModalInsumos] = useState(false);
    const [insumoModal, setInsumoModal] = useState({ nombre: "", cantidad: "", unidad: "", costo: 0, insumos: [] });
    const [nuevoInsumo, setNuevoInsumo] = useState(null);
    const [cantidadNuevoInsumo, setCantidadNuevoInsumo] = useState(0)

    const handleClose = () => setModalInsumos(false);
    const closeModalAgregarInsumo = () => setShowAgregarInsumo(false);
    const openAgregarInsumo = () => setShowAgregarInsumo(true);
    const crear = () => {
        setInsumo({ nombre: "", cantidad: "", unidad: "", costo: 0, insumos: [] })
        setShowNuevo(true)
    }
    const mostrarInsumos = (ins) => {
        setInsumoModal(ins)
        setModalInsumos(true)
    }
    const hide = () => {
        if (showNuevo) {
            setShowNuevo(false)
        } else if (showEditar) {
            setShowEditar(false)
        }
    }

    function editar(index) {
        setInsumo(insumos[index])
        if (showNuevo) {
            setShowEditar(false)
        } else {
            setShowEditar(true)
        }
    }
    function enviarDatos() {
        var temp = insumo;
        var secondTemp = [];
        if (showNuevo) {
            temp.insumos.forEach(element => {
                secondTemp.push({ insumo_insumoId: element.insumoId, cantidadUsada: element.cantidadUsada })
            });
            temp.insumos = secondTemp

            axios.post(environment.baseApiUrl + "/productos", temp).then((data) => {
                setShowNuevo(false)
                setInsumos([])
                setInsumo({ nombre: "", cantidad: "", unidad: "", costo: 0, insumos: [] })

                cargarinsumos()
            })
        } else if (showEditar) {
            temp.insumos.forEach(element => {
                //delete element['pivot']
                secondTemp.push({ insumo_insumoId: element.pivot.insumo_insumoId, cantidadUsada: element.pivot.cantidadUsada })
            });
            temp.insumos = secondTemp
            axios.patch(environment.baseApiUrl + "/productos/" + insumo.productoId, temp).then((data) => {
                setShowEditar(false)
                setInsumos([])
                setInsumo({ nombre: "", cantidad: "", unidad: "", costo: 0, insumos: [] })
                cargarinsumos()
            })
        }

    }
    function filtrar(event) {
        var keyword = event.target.value
        setInsumos(insumosOriginales.filter(c =>
            c.nombre.toLowerCase().includes(keyword.toLowerCase()) || c.ubicacion.toLowerCase().includes(keyword.toLowerCase()) || c.mail.toLowerCase().includes(keyword.toLowerCase())))

    }
    function onChange(event, id) {
        insumo[id] = event.target.value
    }
    function cargaInsumos() {
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/insumos", { withCredentials: true }).then((data) => {
            setInsumosVerdaderos(data.data.data)
        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }
    const agregarNuevoInsumo = () => {
        if (nuevoInsumo != null) {
            if (cantidadNuevoInsumo != null) {
                var temp = nuevoInsumo;
                temp.cantidadUsada = Number(cantidadNuevoInsumo);
                //Fix para listar los insumos
                temp.insumo_insumoId = temp.insumoId
                temp.precioVenta = Number(temp.precioVenta)
                temp.pivot = { cantidadUsada: cantidadNuevoInsumo, insumo_insumoId: temp.insumo_insumoId }
                insumo.insumos.push(temp)
                closeModalAgregarInsumo()
            }
        }
    }

    const guardarInsumoNuevoProducto = (id) => {
        // eslint-disable-next-line
        var insum = insumosVerdaderos.filter(ins => ins.insumoId == id);
        if (insum != null) {

            setNuevoInsumo(insum[0]);
        }
    }
    const guardarCantidadInsumoNuevoProducto = (cantidad) => {
        if (nuevoInsumo != null) {
            setCantidadNuevoInsumo(cantidad);
        }
    }

    function cargarinsumos() {//CargarProductos
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/productos", { withCredentials: true }).then((data) => {
            setInsumos(data.data.data)
            setInsumosOriginales(data.data.data)
        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }
    useEffect(() => {
        cargaInsumos()
        cargarinsumos()
    }, [])

    return (

        <Container className='mt-3'>

            <Modal show={showAgregarInsumo} onHide={closeModalAgregarInsumo}>
                <Modal.Header className="d-block">
                    <Modal.Title className="text-center">Agregar Insumos</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form.Group controlId="exampleForm.ControlSelect1">
                        <Form.Label>Insumos</Form.Label>
                        <Form.Control as="select" defaultValue="0" onChange={(e) => guardarInsumoNuevoProducto(e.target.value)}>
                            <option value="0" disabled>Seleccione un insumo</option>
                            {insumosVerdaderos.map((value, index) => {
                                return <option key={index} value={value.insumoId}>{value.nombre}</option>
                            })}
                        </Form.Control>
                    </Form.Group>

                    <div className="fg" style={{ height: 60 }}>
                        <input type="number" placeholder=" " style={{ width: "100%" }} onChange={(e) => guardarCantidadInsumoNuevoProducto(e.target.value)}></input>
                        <label>Cantidad</label>
                    </div>
                    <div className="text-center">
                        <button onClick={(e) => agregarNuevoInsumo()}>Agregar</button>
                    </div>
                </Modal.Body>

            </Modal>

            <Modal show={modalInsumos} onHide={handleClose}>
                <Modal.Header className="d-block">
                    <Modal.Title className="text-center">Insumos de {insumoModal.nombre}</Modal.Title>
                </Modal.Header>
                <Modal.Body style={{ paddingLeft: "0", paddingRight: "0" }}>
                    <div className="itemProducto">
                        {insumoModal.insumos.map((value, index) => {
                            return <div key={index} className="text-center">{value.nombre} <strong>|</strong> Cantidad utilizada:{value.pivot.cantidadUsada}</div>
                        })}
                    </div>

                    <div className="text-center mt-5">
                        <button onClick={handleClose}>Cerrar</button>
                    </div>

                </Modal.Body>
            </Modal>




            {!showNuevo && !showEditar ? (
                <Fragment>
                    <Row className="mb-3">
                        <Col>
                            <Card style={{ display: "inline-block", width: "100%" }}>
                                <Row>
                                    <Col xs={3} md={2} ><button onClick={crear}>Agregar</button></Col>
                                    <Col xs={9} md={8}><input onChange={filtrar} className="search-field" placeholder="Escribe lo que deseas buscar"></input></Col>
                                    <Col xs={0} md={2} style={{ textAlign: "end" }}></Col>
                                </Row>
                            </Card>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Table bordered hover>
                                <thead>
                                    <tr>
                                        <th width="20%">Nombre</th>
                                        <th width="30%">Existencia</th>
                                        <th width="30%">Precio de Venta</th>
                                        <th width="20%"></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {insumos.map((value, index) => {
                                        return <tr key={index} >
                                            <td onClick={e => editar(index)}><div className="td-tag">Nombre</div>{value.nombre}</td>
                                            <td onClick={e => editar(index)}><div className="td-tag">Existencia</div>{value.cantidad}</td>
                                            <td onClick={e => editar(index)}><div className="td-tag">Precio de venta</div>${value.precioVenta}</td>
                                            <td><button className="borderless" onClick={e => mostrarInsumos(value)}>Ver insumos</button></td>
                                        </tr>
                                    })}

                                </tbody>
                            </Table>
                        </Col>
                    </Row>
                </Fragment>

            ) : (
                    <Fragment>
                        <Row className="mb-3">
                            <Col>
                                <Card style={{ display: "inline-block", width: "100%" }}>
                                    <Row>
                                        <Col xs={3} md={2}><button onClick={hide}>Volver</button></Col>
                                        <Col xs={6} md={8} style={{ display: 'flex', alignItems: "center", justifyContent: 'center' }}><h5>{(showNuevo) ? (<div>Nuevo Producto</div>) : (<div>Editar Producto</div>)}</h5></Col>
                                        <Col xs={3} md={2}></Col>
                                    </Row>
                                </Card>
                            </Col>
                        </Row>
                        <Card className="py-4">
                            <Row>
                                <Col className=" d-flex justify-content-center"><Input type="text" label="Nombre" value={insumo.nombre} readOnly={showEditar} onChange={e => onChange(e, "nombre")}></Input></Col>
                                <Col className=" d-flex justify-content-center"><Input type="text" label="Existencia" value={insumo.cantidad} onChange={e => onChange(e, "cantidad")}></Input></Col>
                                <Col className=" d-flex justify-content-center"><Input type="text" label="Precio de venta" value={insumo.precioVenta} readOnly={showEditar} onChange={e => onChange(e, "precioVenta")}></Input></Col>
                            </Row>
                            <Row className="mt-3">
                                <Col className={`justify-content-center ${showNuevo ? "text-right" : "text-center"}`}><h5>Insumos</h5></Col>
                                {showNuevo && <Col style={{ alignSelf: "center" }}>
                                    <button className="compact" onClick={openAgregarInsumo}>Agregar</button>
                                </Col>
                                }
                            </Row>
                            <Row>

                                {insumo.insumos ? (
                                    <Col className="itemProducto">{
                                        insumo.insumos.map((value, index) => {
                                            return <div key={index} className="text-center">{value.nombre} <strong>|</strong> Cantidad utilizada:{value.pivot.cantidadUsada}</div>
                                        })
                                    }

                                    </Col>
                                ) : (
                                        <Col className="text-center py-5">
                                            <div>No hay insumos cargados</div>
                                        </Col>

                                    )}



                            </Row>
                            <Row className="justify-content-center mt-5">
                                <button onClick={enviarDatos}>Guardar</button>
                            </Row>
                        </Card>
                    </Fragment>
                )}
        </Container>
    )
}
export default Productos;