import React, { useEffect, useState, Fragment } from 'react';
import { Table, Container, Row, Col, Card, Modal, Form } from 'react-bootstrap';
import Input from './Inputs'
import axios from 'axios';
import environment from './../env';
import Util from './../Util.js'

const Ventas = () => {
    const [valorVenta, setValorVenta] = useState(0)
    const [productosVenta, setProductosVenta] = useState([])
    const [cantidadProductoSeleccionado, setCantidadProductoSeleccionado] = useState(0)
    const [productoSeleccionado, setProductoSeleccionado] = useState(null)
    const [productos, setProductos] = useState([])
    const [ventas, setVentas] = useState([])
    const [clientes, setClientes] = useState([])
    const [ventasOriginales, setVentasOriginales] = useState([])
    const [showNuevo, setShowNuevo] = useState(false)
    const [showAgregarProducto, setShowAgregarProducto] = useState(false);
    const [showDetalleVenta, setShowDetalleVenta] = useState(false)
    const [ventaDetalle, setVentaDetalle] = useState({ cliente: {}, producto_venta: [] })
    const [venta, setVenta] = useState({ nombre: "", ubicacion: "", mail: "" });
    const [forma, setForma] = useState("")

    const openAgregarProducto = () => setShowAgregarProducto(true);
    const closeModalAgregarProducto = () => {
        setShowAgregarProducto(false);
        setProductoSeleccionado(null)
        console.log(productoSeleccionado)
    }
    const closeModalDetalleVenta = () => {
        setShowDetalleVenta(false)
    }
    const mostrarDetalleVenta = (value) => {
        setVentaDetalle(value)
        setShowDetalleVenta(true)
    }

    const cambiarProductoSeleccionado = (id) => {
        // eslint-disable-next-line
        var prod = productos.filter(p => p.productoId == id);
        if (prod != null) {
            setProductoSeleccionado(prod[0]);
        }
    }

    const agregarProducto = () => {
        var array = productosVenta;
        var copia = productoSeleccionado;
        var valorTemp = valorVenta;
        //copia.precio = copia.precioVenta
        copia.cantidad = cantidadProductoSeleccionado;
        array.push(copia)
        valorTemp += (Number(cantidadProductoSeleccionado) * Number(copia.precioVenta));
        setValorVenta(valorTemp)
        setProductosVenta(array)
        venta.productos = array
        venta.total = valorTemp
        closeModalAgregarProducto()
    }

    const cargarProductos = () => {
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/productos", { withCredentials: true }).then((data) => {
            //console.log(data)
            setProductos(data.data.data)
        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }

    function crear() {
        setVenta({})
        setShowNuevo(true)
    }
    function hide() {
        if (showNuevo) {
            setShowNuevo(false)
        }
    }

    function enviarDatos() {
        if (showNuevo) {
            axios.post(environment.baseApiUrl + "/ventas", venta).then((data) => {
                console.log(data)
                setShowNuevo(false)
                setForma('')
                setValorVenta(0)
                setVenta({ nombre: "", ubicacion: "", mail: "" })
                setProductosVenta([])
                cargarventas()
            })
        }

    }
    function filtrar(event) {
        var keyword = event.target.value
        setVentas(ventasOriginales.filter(c =>
            c.nombre.toLowerCase().includes(keyword.toLowerCase()) || c.ubicacion.toLowerCase().includes(keyword.toLowerCase()) || c.mail.toLowerCase().includes(keyword.toLowerCase())))

    }
    function onChange(event, id) {
        venta[id] = event.target.value
        var temp;
        // eslint-disable-next-line
        if (venta.formaPago == "cuenta") {
            temp = <Col md="8" className=" d-flex justify-content-center"><Input type="date" label="A cobrar el" value={venta.fechaPendiente} onChange={e => onChange(e, "fechaPendiente")}></Input></Col>;
        }
        setForma(temp)
    }
    function cargarventas() {
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/ventas", { withCredentials: true }).then((data) => {
            setVentas(data.data.data)
            console.log(data.data.data)
            setVentasOriginales(data.data.data)
        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }
    function cargarClientes() {
        axios.defaults.withCredentials = true;
        axios.get(environment.baseApiUrl + "/clientes", { withCredentials: true }).then((data) => {
            setClientes(data.data)

        }).catch((e) => {
            //Es necesario hacer catch a los 401 y desloguear a la persona
            console.log(e)
        })
    }
    useEffect(() => {
        cargarProductos();
        cargarventas()
        cargarClientes()
    }, [])

    return (

        <Fragment>

            <Modal show={showDetalleVenta} onHide={closeModalDetalleVenta}>
                <Modal.Header className="d-block">
                    <Modal.Title className="text-center">Detalle de Venta</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Row><Col><b>Fecha:</b> {Util.formatDate(ventaDetalle.fecha)}</Col></Row>
                    <Row><Col><b>Cliente:</b> {ventaDetalle.cliente.nombre}</Col></Row>
                    <Row><Col><b>Total:</b> ${ventaDetalle.total}</Col></Row>
                    <div className="itemProducto">
                        {ventaDetalle.producto_venta.map((value, index) => {
                            return <div key={index} className="text-center">{value.producto.nombre} a ${value.precio} X {value.cantidad}</div>
                        })}
                    </div>

                </Modal.Body>
            </Modal>

            <Modal show={showAgregarProducto} onHide={closeModalAgregarProducto}>
                <Modal.Header className="d-block">
                    <Modal.Title className="text-center">Agregar Producto</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form.Group controlId="exampleForm.ControlSelect1">
                        <Form.Label>Productos</Form.Label>
                        <Form.Control as="select" defaultValue="0" onChange={(e) => cambiarProductoSeleccionado(e.target.value)}>
                            <option value="0" disabled>Seleccione un producto</option>
                            {productos.map((value, index) => {
                                return <option key={index} value={value.productoId}>{value.nombre}</option>
                            })}
                        </Form.Control>
                    </Form.Group>

                    {
                        productoSeleccionado != null &&
                        <Fragment>
                            <div>En existencia: {productoSeleccionado.cantidad}</div>
                            <div>Precio: ${productoSeleccionado.precioVenta}</div>
                        </Fragment>

                    }

                    <div className="fg" style={{ height: 60, marginTop: "0.5rem" }}>
                        <input type="number" placeholder=" " style={{ width: "100%" }} onChange={(e) => setCantidadProductoSeleccionado(e.target.value)}></input>
                        <label>Cantidad</label>
                    </div>
                    <div className="text-center">
                        <button onClick={agregarProducto}>Agregar</button>
                    </div>
                </Modal.Body>
            </Modal>

            <Container className='mt-3'>
                {!showNuevo ? (
                    <Fragment>
                        <Row className="mb-3">
                            <Col>
                                <Card style={{ display: "inline-block", width: "100%" }}>
                                    <Row>
                                        <Col xs={3} md={2} ><button onClick={crear}>Agregar</button></Col>
                                        <Col xs={9} md={8}><input onChange={filtrar} className="search-field" placeholder="Escribe lo que deseas buscar"></input></Col>
                                        <Col xs={0} md={2} style={{ textAlign: "end" }}></Col>
                                    </Row>
                                </Card>
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                <Table bordered hover>
                                    <thead>
                                        <tr>
                                            <th width="20%">Fecha</th>
                                            <th width="40%">Cliente</th>

                                            <th width="40%">Acciones</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Array.isArray(ventas) && (

                                            ventas.map((value, index) => {
                                                return <tr key={index} >
                                                    <td><div className="td-tag">Fecha</div>{Util.formatDate(value.fecha)}</td>
                                                    <td><div className="td-tag">Cliente</div>{value.cliente.nombre}</td>

                                                    <td><div className="td-tag">Acciones</div><button className="borderless" onClick={e => mostrarDetalleVenta(value)}>Detalle</button></td>
                                                </tr>
                                            })

                                        )}


                                    </tbody>
                                </Table>
                            </Col>
                        </Row>
                    </Fragment>

                ) : (
                        <Fragment>
                            <Row className="mb-3">
                                <Col>
                                    <Card style={{ display: "inline-block", width: "100%" }}>
                                        <Row>
                                            <Col xs={3} md={2}><button onClick={hide}>Volver</button></Col>
                                            <Col xs={6} md={8} style={{ display: 'flex', alignItems: "center", justifyContent: 'center' }}><h5>Nuevo venta</h5></Col>
                                            <Col xs={3} md={2}></Col>
                                        </Row>
                                    </Card>
                                </Col>
                            </Row>
                            <Card className="py-4">
                                <Row className="justify-content-center">
                                    <Col md="8" className=" d-flex justify-content-center"><Input type="date" label="Fecha" value={venta.fecha} onChange={e => onChange(e, "fecha")}></Input></Col>
                                    <Col md="8" className=" d-flex justify-content-center">
                                        <Form.Group controlId="exampleForm.ControlSelect1">
                                            <Form.Label>Cliente</Form.Label>
                                            <Form.Control as="select" defaultValue="0" style={{ width: "250px" }} onChange={(e) => onChange(e, "venta_clienteId")}>
                                                <option value="0" disabled>Seleccione un cliente</option>
                                                {clientes.map((value, index) => {
                                                    return <option key={index} value={value.id}>{value.nombre}</option>
                                                })}
                                            </Form.Control>
                                        </Form.Group>
                                    </Col>
                                    <Col md="8" className=" d-flex justify-content-center">
                                        <Form.Group controlId="exampleForm.ControlSelect1">
                                            <Form.Label>Forma de Pago</Form.Label>
                                            <Form.Control as="select" defaultValue="0" style={{ width: "250px" }} onChange={e => onChange(e, "formaPago")}>
                                                <option value="0" disabled>Seleccione una forma de pago</option>
                                                <option value="efectivo">Efectivo</option>
                                                <option value="cuenta">A cuenta</option>
                                            </Form.Control>
                                        </Form.Group>
                                    </Col>
                                    {forma}
                                </Row>

                                <Row className="mt-3">
                                    <Col className="justify-content-center text-right"><h5>Productos</h5></Col>
                                    <Col style={{ alignSelf: "center" }}><button className="compact" onClick={openAgregarProducto}>Agregar</button></Col>
                                </Row>
                                <Row>
                                    {productosVenta ? (
                                        <Col className="itemProducto">{
                                            productosVenta.map((value, index) => {
                                                return <div key={index} className="text-center">{value.nombre} X {value.cantidad}</div>
                                            })
                                        }

                                        </Col>
                                    ) : (
                                            <Col className="text-center py-5">
                                                <div>No hay insumos cargados</div>
                                            </Col>

                                        )}
                                </Row>
                                <Row>
                                    <Col className="text-center mt-5"><h5>Subtotal: ${valorVenta}</h5></Col>
                                </Row>
                                <Row className="justify-content-center mt-5">
                                    <button onClick={enviarDatos}>Confirmar Venta</button>
                                </Row>
                            </Card>
                        </Fragment>
                    )}
            </Container>
        </Fragment >
    )
}
export default Ventas;